import axios from 'axios';
import apiConstants from '../../constants/api';
import tickerFactory from './ticker-factory';
import currencyPairsList from '../../constants/currency-pairs';

function get(currencyPair) {
  return axios.get(`${'https://cors-anywhere.herokuapp.com/'}${apiConstants.CRPTO_API_URL}/ticker/${currencyPair}`)
    .then(payload => tickerFactory.createTickerFromPayload(payload))
    .catch(e => console.log(e));
}

function getTickerList() {
  return get(currencyPairsList[0].value).then(
    data => data);
  //return currencyPairsList.map(el => ({ name: el.name, data: get(el.value).then(data => data) }));
}

export default {
  get,
  getTickerList,
};
